@font-face {
  font-family: PixcelMplus12;
  src: url("../../font/PixelMplus12-Regular.eot");
  src: url("../../font/PixelMplus12-Regular.eot?#iefix") format("embedded-opentype"), url("../../font/PixelMplus12-Regular.woff") format("woff"), url("../../font/PixelMplus12-Regular.ttf") format("truetype"), url("../../font/Pixelmplus12-Regular.svg#svgFontName") format("svg"); }

* {
  box-sizing: border-box; }
  *:before, *:after {
    box-sizing: border-box; }

body {
  font-family: PixcelMplus12, Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background: #555555; }

header {
  color: #d9edf7;
  background: #555555; }
  header h1 {
    display: inline-block;
    padding: 0 20px; }
  header a {
    display: inline-block;
    margin: 10px;
    padding: 10px;
    color: #d9edf7;
    text-decoration: none;
    border-radius: 3px; }
    header a.active, header a:hover {
      background-color: #888888; }

a {
  text-decoration: none;
  color: #91C3DC; }

section {
  padding: 20px;
  color: #d9edf7; }
